<template>
  <button :disabled="loading">
    <span v-if="!loading">
      <slot></slot>
    </span>
    <span v-else class="spinner-border spinner-border-sm" role="status" aria-hidden="true"></span>
  </button>
</template>

<script>
  export default {
    name: "ButtonLoading",
    props: {
      loading: { type: Boolean, default: false }
    }
  }
</script>

