<template>
  <div>
    <div class="alert alert-danger text-center">
      Reportar
    </div>
    <form @submit.prevent="submit" class="container">
      <label for="ipt-texto">Descrição</label>
      <textarea id="ipt-texto"
                rows="10"
                placeholder="descreva o problema detalhadamente..."
                maxlength="500"
                class="form-control"
                required
                :disabled="loading"
                autocapitalize="none"
                v-model="descricao"></textarea>
      <button-loading class="btn btn-primary btn-block mt-3" type="submit" :loading="loading">Enviar</button-loading>
    </form>
  </div>
</template>

<script>
  import ButtonLoading from "../components/ButtonLoading";
  export default {
    name: "Report",
    components: {ButtonLoading},
    data: () => ({
      descricao: '',
      loading: false
    }),
    methods: {
      async submit() {
        if (!this.descricao.trim()) {
          this.$toastr.info('Descreva o problema');
          return;
        }
        let formData = new FormData();
        formData.append("descricao", this.descricao);
        this.loading = true;
        try {
          await this.$http.appApi.post('/suporte/report.php', formData, {headers: {'Authorization': this.$store.state.token}});
          this.$toastr.success('Agradecemos a sua colaboração', 'Mensagem registrada');
          await this.$router.push({ name: 'home' });
        } catch (e) {
          this.loading = false;
        }
      }
    }
  }
</script>

